.btn {
  margin-top: 2rem;
  padding: 1.5rem 3rem;
  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  letter-spacing: 0.2rem;
  background: -webkit-gradient(linear, left top, right top, from(#00acdb), color-stop(50%, #00acdb), color-stop(50%, #fff), to(#fff));
  background: linear-gradient(to right, #00acdb 0%, #00acdb 50%, #fff 50%, #fff 100%);
  background-size: 200% 110%;
  background-position: 100% 0;
  -webkit-transition: background-position .3s, color .3s;
  transition: background-position .3s, color .3s;
  border: 2px solid #00acdb;
  background-color: #fff;
  color: black;
}
.btn:hover {
    background-position: 0 0;
    color: #fff;
}
.btn--small {
    padding: .5rem 2rem !important;
    font-size: 1.2rem !important;
}
.btn--bottomRight {
    position: absolute;
    right: 2rem;
    bottom: 2rem;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
h1, .h1 {
  font-size: 8rem;
  font-weight: 700;
}
h1.primary, .h1.primary {
    text-align: left;
    color: #fff;
    margin: 0 1rem;
}
@media (max-width: 1000px) {
h1.primary, .h1.primary {
        font-size: 6rem;
        text-align: center;
}
}
@media (max-width: 500px) {
h1.primary, .h1.primary {
        font-size: 4rem;
        text-align: center;
}
}
h1.secondary, .h1.secondary {
    text-align: center;
    color: #00acdb;
}
@media (max-width: 1000px) {
h1.secondary, .h1.secondary {
        font-size: 6rem;
}
}
@media (max-width: 700px) {
h1.secondary, .h1.secondary {
        font-size: 4rem;
}
}
h2.primary, .h2.primary {
  font-size: 1.3rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #fff;
  display: inline-block;
}
@media (max-width: 1000px) {
h2.primary, .h2.primary {
      text-align: center;
      display: block;
      margin: 0 1rem;
      margin-top: 2rem;
}
h2.primary:after, .h2.primary:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 1.5rem;
        margin-left: 1.125rem;
        border-bottom: 1px solid #fff;
        margin-right: .5rem;
        -webkit-transition: .3s width;
        transition: .3s width;
}
}
h2.primary:before, .h2.primary:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    width: 1.5rem;
    margin-right: 1.125rem;
    border-bottom: 1px solid #fff;
    margin-left: .5rem;
    -webkit-transition: .3s width;
    transition: .3s width;
}
@media (max-width: 1000px) {
h2.primary, .h2.primary {
      text-align: center;
}
}
h2.secondary, .h2.secondary {
  max-width: 70rem;
  margin: 1rem auto;
  padding: 0 2rem;
  text-align: center;
  color: #3f3f3f;
  font-weight: 300;
  font-size: 1.8rem;
}
.closing {
  padding: 5rem;
  text-align: center;
}
@media (max-width: 700px) {
.closing {
      padding: 5rem 3rem;
}
}
.closing__title {
    font-size: 5rem;
    color: #00acdb;
}
@media (max-width: 700px) {
.closing__title {
        font-size: 4rem;
}
}
.closing__statement {
    max-width: 70rem;
    margin: 0 auto;
    text-align: center;
    color: #3f3f3f;
    font-weight: 300;
    font-size: 1.6rem;
}
.section--news {
  padding: 10rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10rem;
}
@media (max-width: 1000px) {
.section--news {
      grid-template-columns: 1fr;
      grid-gap: 5rem;
      padding: 5rem;
}
.section--news .box--left, .section--news .box--right {
        justify-self: center !important;
}
}
.section--news.in-viewport .box--right {
    -webkit-animation-name: fadeInRight;
            animation-name: fadeInRight;
}
.section--news.in-viewport .newsInfo .news__items {
    -webkit-animation-name: fadeInUp;
            animation-name: fadeInUp;
}
.section--news.in-viewport .newsInfo .news__items:nth-child(3) {
      -webkit-animation-delay: .3s;
              animation-delay: .3s;
}
.section--news.in-viewport .newsInfo .news__items:nth-child(4) {
      -webkit-animation-delay: .6s;
              animation-delay: .6s;
}
.section--news .box {
    width: 30rem;
}
.section--news .box--left {
      justify-self: right;
}
.section--news .box--right {
      justify-self: left;
      -ms-flex-item-align: center;
          align-self: center;
      opacity: 0;
      visibility: hidden;
      -webkit-animation-duration: 1s;
              animation-duration: 1s;
      -webkit-animation-fill-mode: both;
              animation-fill-mode: both;
}
.section--news .newsInfo__title {
    font-size: 2.5rem;
    letter-spacing: 0.1rem;
}
.section--news .newsInfo__text {
    font-weight: 300;
}
.section--news .newsInfo__text p {
      margin-top: 2rem;
}
.section--news .news__items {
    position: relative;
    margin-top: 5rem;
    opacity: 0;
    visibility: hidden;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}
